import React, {Component} from 'react';

export class ErrorBox extends Component {
  render() {
    return (
      <box label='!!! ERROR !!!' border={{type: 'line'}} style={{border: {fg: 'red'}}}>
        {this.props.error.toString()}
      </box>
    );
  }
}



